@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

.pg-04 {
    font-family: 'Poppins', sans-serif;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 6%;
    background-color: #111111;
    transition: background-color 0.5s ease;
}

.inner-pg-04 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem 5%;
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    height: max-content;
    width: max-content;
}

.pg-04 .inner-pg-04 .contact h1 {
    font-size: 5.5rem;
    color: #FFF;
    transition: all 0.5s ease;
}

.pg-04 .inner-pg-04 .contact h1 span {
    color: #FFB72B;
}

.pg-04 .inner-pg-04 .contact-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.pg-04 .inner-pg-04 .contact-content a {
    width: 100px;
    height: 100px;
    transition: all 0.5s ease;
}

.pg-04 .inner-pg-04 .contact-content a svg {
    /* fill: #bd94ffca; */
    fill: #606f6f;
    width: inherit;
    height: inherit;
    transition: fill 0.3s ease;
}

@media (pointer: fine) {
    .pg-04 .inner-pg-04 .contact-content a svg:hover {
        fill: #9153F4;
        transition: fill 0.3s ease;
    }
    
    .pg-04 .inner-pg-04 .contact-content a svg:active {
        transform: scale(0.8);
        transition: all 0.3s ease;
    }
}

@media screen and (max-width: 768px) {
    .pg-04 .inner-pg-04 .contact h1{
        font-size: 4.5rem;
        color: #FFF;
    }

    .pg-04 .inner-pg-04 .contact-content a {
        width: 100px;
        height: 100px;
    }

    .pg-04 .inner-pg-04 .contact-content a svg {
        width: inherit;
        height: inherit;
    }
}

@media screen and (max-width: 560px) {
    .pg-04 .inner-pg-04 .contact h1{
        font-size: 3.7rem;
        color: #FFF;
    }
    
    .pg-04 .inner-pg-04 .contact-content {
        margin-top: 10px;
    }

    .pg-04 .inner-pg-04 .contact-content a {
        width: 85px;
        height: 85px;
    }

    .pg-04 .inner-pg-04 .contact-content a svg {
        width: inherit;
        height: inherit;
    }
}

@media screen and (max-width: 425px) {
    .pg-04 .inner-pg-04 .contact h1{
        font-size: 3rem;
        color: #FFF;
    }

    .pg-04 .inner-pg-04 .contact-content {
        margin-top: 10px;
        flex-direction: column;
    }

    .pg-04 .inner-pg-04 .contact-content a {
        width: 100px;
        height: 100px;
    }

    .pg-04 .inner-pg-04 .contact-content a svg {
        width: inherit;
        height: inherit;
    }
}