@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

.pg-02 {
    font-family: 'Poppins', sans-serif;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 6%;
    background-color: #111111;
    transition: background-color 0.5s ease;
}

.inner-pg-02 {
    display: flex;
    flex-direction: column;
    padding: 2rem 2%;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
}

.inner-pg-02::-webkit-scrollbar {
    width: 0!important;
}

.skill {
    position: fixed;
    /* left: 0; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 84.6vw;
    height: max-content;
}

.skill .skillname, .project-lnk {
    /* position: fixed; */
    font-size: 1.2rem;
    font-weight: 400;
    letter-spacing: 1.2px;
    color: aliceblue;
    width: max-content;
    text-decoration: none!important;
    transition: color 0.2s ease;
}

.skill .project-lnk:hover {
    color: #FFB72B;
    transition: color 0.2s ease;
}

@media (pointer: fine) {
    .skill .project-lnk:active {
        transform: scale(0.9);
        transition: all 0.3s ease;
    }
}

.skill .project-lnk::after {
    content: '';
    position: absolute;
    top: 85%;
    right: 0;
    border: 0;
    width: 0;
    border: 0;
    background-color: #FFB72B;
    transition: border 0.2s ease,
                width 0.2s ease;
}

.skill .project-lnk:hover::after {
    animation: stretch0 0.4s forwards;
    transition: border 0.2s ease;
}

@keyframes stretch0 {
    0% {
        width: 0;
        border: 0;
    }
    10% {
        width: 17px;
        border: 1px solid #FFB72B;
    }
    20% {
        width: 27px;
        border: 1px solid #FFB72B;
    }
    30% {
        width: 37px;
        border: 1px solid #FFB72B;
    }
    40% {
        width: 47px;
        border: 1px solid #FFB72B;
    }
    50% {
        width: 57px;
        border: 1px solid #FFB72B;
    }
    60% {
        width: 67px;
        border: 1px solid #FFB72B;
    }
    70% {
        width: 77px;
        border: 1px solid #FFB72B;
    }
    80%,90%,100% {
        width: 86.6px;
        border: 1px solid #FFB72B;
    }
}

.skill .project-lnk {
    margin-bottom: 0.5rem;
}

.inner-pg-02 .skill-content {
    display: flex;
    flex-direction: column;
    margin-top: 40px;
}

.inner-pg-02 .skill-content .skl-link {
    color: #fff;
    text-decoration: none;
    display: block;
    line-height: 1.1;
    letter-spacing: -0.5rem;
    margin-top: 30px;
    width: max-content;
    transition: color 0.3s ease;
}

.inner-pg-02 .skill-content .skl-link:hover {
    color: #e3e3e3f5;
    transition: color 0.3s ease;
}

@media (pointer: fine) {
    .inner-pg-02 .skill-content .skl-link:active {
        transform: scale(0.8)!important;
        transition: all 0.3s ease;
    }
}

.inner-pg-02 .skill-content .skl-link .skl-name {
    font-size: 14rem;
    font-weight: 700;
}

.inner-pg-02 .skill-content .skl-link .skl-name::before {
    content: '';
    position: absolute;
    top: 65%;
    width: 104%;
    color: #9153F4;
    opacity: 0;
    border: 28px solid #9153F4;
    background-color: #9153F4;
    border-radius: 2px;
    transition: all 0.3s ease;
    transition-property: opacity, color, border;
    z-index: -1;
}

.inner-pg-02 .skill-content .skl-link .skl-name:hover::before {
    opacity: 1;
    transition: all 0.3s ease;
}

.inner-pg-02 .skill-content .loop__clone {
    transform: translateX(0)!important;
    transition: none!important;
}

