@media (pointer: fine) {
    .cursor {
        fill: #FFDAC7;
        position: fixed;
        top: 0;
        left: 0;
        display: block;
        pointer-events: none;
        transform: translate(-50%, -50%) scale(2);
        transition: all 0.3s ease;
        transition-property: fill, stroke, stroke-width, transform;
    }

    .cursor-style {
        fill: transparent;
        stroke: #FFDAC7;
        stroke-width: 0.2;
        transform: translate(-50%, -50%) scale(9);
    }
}

@media (pointer: coarse) {
    .cursor {
        display: none;
    }
}

