.home-btn {
    width: max-content;
    height: max-content;
    position: absolute;
    top: 84%;
    /* left: calc(100% - 225px); */
    left: calc(100% - 150px);
}

.home-btn a {
    text-decoration: none;
    width: 60px;
    height: 60px;
    transition: transform 0.3s ease;
    /* color: antiquewhite; */
}

.home-btn a svg {
    fill: #7F00FF;
    width: inherit;
    height: inherit;
    filter: drop-shadow(0 0.2rem 0.25rem rgba(0, 0, 0, 0.2));
    transition: all 0.3s ease;
}

.light-dark-mode-btn {
    width: max-content;
    height: max-content;
    position: absolute;
    top: 84%;
    left: calc(100% - 150px);
}

.light-dark-mode-btn a {
    text-decoration: none;
    width: 60px;
    height: 60px;
    transition: transform 0.3s ease;
}

.light-dark-mode-btn a svg {
    fill: #FFF;
    width: inherit;
    height: inherit;
    filter: drop-shadow(0 0.2rem 0.25rem rgba(0, 0, 0, 0.2));
    cursor: pointer;
    transition: all 0.3s ease;
}

@media (pointer: fine) {
    .home-btn a svg:hover, .light-dark-mode-btn a svg:hover {
        transform: scale(1.2);
        transition: transform 0.3s ease;
    }

    .home-btn a svg:active, .light-dark-mode-btn a svg:active {
        transform: scale(1);
        transition: transform 0.3s ease;
    }
}

@media screen and (max-width: 768px) {
    .home-btn {
        top: 87%;
        left: calc(100% - 120px);
    }
    
    .home-btn a {
        width: 50px;
        height: 50px;
    }
}

@media screen and (max-width: 768px) {
    .home-btn {
        top: 87%;
        left: calc(100% - 80px);
    }
    
    .home-btn a {
        width: 50px;
        height: 50px;
    }
}



